<template>
    <button class="capsule-button" @click="navigate">
      {{ buttonText }}
    </button>
  </template>
  
  <script>
  export default {
    props: {
      buttonText: {
        type: String,
        required: true
      },
      targetRoute: {
        type: String,
        required: true
      }
    },
    methods: {
      navigate() {
        this.$router.push(this.targetRoute);
      }
    }
  };
  </script>
  
  <style scoped>
  .capsule-button {
    
    background-color: #0a364a; /* 墨绿色背景 */
    color: white;
    border: none;
    border-radius: 25px; /* 胶囊形状 */
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
  
    padding: 15px 30px; /* 增大内边距以增大按钮大小 */
  font-size: 16px; /* 增大字体大小 */
  border-radius: 30px; /* 增大边框半径以保持胶囊形状 */

    /* 鼠标悬停时改变背景颜色 */
    &:hover {
      background-color: #18606e;
    }
  }
  </style>
  